
import React from 'react';
import PropTypes from 'prop-types';

import { Panel, Checkbox, PanelSpinner, Badge, Counter, PanelHeader, Header, TabbarItem, Button, Group, Tabbar, Cell, Div, Avatar, CellButton, Link, List, HeaderButton, platform, removeEventListener, PanelHeaderBack, FormItem, SliderSwitch, View } from '@vkontakte/vkui';



import persik from '../img/persik.png';
import './Help.css';

import { Icon28NewsfeedOutline } from '@vkontakte/icons'; 
import { Icon28Profile } from '@vkontakte/icons'; 
import { Icon28Settings } from '@vkontakte/icons'; 
import { Icon28GameOutline } from '@vkontakte/icons'; 





const ColoredLine = ({ color }) => (
  <hr
      style={{
          color: color,
          backgroundColor: color,
          width: 650,
          
      }}
  />
);

const Bas = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="home"/>}
		>
			Бонусы
		</PanelHeader>
    
      
                

    <div>
        <center><h3>Получайте Бонусы от Las Vegas World!</h3></center>
    </div>

<center>
    <Div>
       <Button href="@lasvegasworldlv-eto">35.000 VK coin, за репост</Button>
     </Div>
				
     
     
</center>
 



    <Tabbar>
      
      <TabbarItem onClick={props.go} data-to="Help" text="Новости">
        <Icon28NewsfeedOutline  />
      </TabbarItem>
	  <TabbarItem  onClick={props.go} data-to="Bas" indicator={<Counter size="s" ></Counter>} text="Бонусы">
        <Icon28GameOutline  />
      </TabbarItem>
      <TabbarItem onClick={props.go} data-to="Prof" indicator={<Badge mode="" aria-label="Новый раздел"  />} text="Настройки">
        <Icon28Settings />
      </TabbarItem>
</Tabbar>


<ColoredLine color="red"  />

<div>
        <center><h3>Промо код, от Las Vegas World!</h3>
        <div class="border-style-solid">  Промо: "Vedasis"</div>
        </center>
    </div>

    <h5>Каждые 3 дня слив промо-кодов! </h5>

    <img className="Persik" src={persik} alt="Persik The Cat"/>

	</Panel>
);




Bas.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};




export default Bas;









