import React from 'react';
import PropTypes from 'prop-types';

import { Panel, Checkbox, Tabs, MiddlewareAPI, AdaptivityProvider, AppRoot, SimpleCell, PanelHeaderSubmit, Badge, PanelHeaderButton, Counter, PanelHeader, Header, TabbarItem, Button, Group, Tabbar, Cell, Div, Avatar, CellButton, Link, List, HeaderButton, platform, removeEventListener, PromoBanner, PanelHeaderBack, FormItem, SliderSwitch, View } from '@vkontakte/vkui';

import { Icon28NewsfeedOutline } from '@vkontakte/icons'; 
import { Icon28SunOutline } from '@vkontakte/icons'; 
import { Icon28Settings } from '@vkontakte/icons'; 
import { Icon28GameOutline } from '@vkontakte/icons'; 

import bridge from '@vkontakte/vk-bridge';
import '@vkontakte/vkui';


// Sends event to client
bridge.send('VKWebAppInit');

// Subscribes to event, sended by client
bridge.subscribe(e => console.log(e));

bridge.send("VKWebAppShowNativeAds", {ad_format:"interstitial"})
.then(data => console.log(data.result))
.catch(error => console.log(error));

bridge.send("VKWebAppGetAuthToken", {"app_id": 7993673, "scope": "friends"});


bridge.send("VKWebAppRetargetingPixel", {"pixel_code": "VK-RTRG-447253-dUuM", "event": "click-to-button"});




const Prof =  props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="home"/>}
		>
			Информация!!
		</PanelHeader>
		

    
   
			
		
        <div>
        <div><center><SimpleCell><h3>{"Настройки/Информация"}</h3></SimpleCell></center></div>
    </div>

	
	
	<Div style={{display: 'flex'}}>
       <Button size="l" stretched style={{ marginRight: 20 ,}}>Темная тема</Button>

       <Button size="l" stretched mode="secondary">Белая тема</Button>
     </Div>

	 
	 <View activePanel="panel">
    <Panel id="panel">
      <Group>
        <FormItem>
        <Checkbox defaultChecked>Получать уведомление</Checkbox>
        </FormItem>
      </Group>
    </Panel>
  </View>

	


<Link href="https://vk.com/@lasvegasworldlv-politika-konfidencialnosti" target="_blank">Политика конфиденциальности</Link>
<Link href="Home">Реклама</Link>









	  <Tabbar>
      
    

      <TabbarItem onClick={props.go} data-to="Help" text="Новости">
        <Icon28NewsfeedOutline  />
      </TabbarItem>
	  <TabbarItem  onClick={props.go} data-to="Bas" indicator={<Counter size="s" ></Counter>} text="Бонусы">
        <Icon28GameOutline  />
      </TabbarItem>
      <TabbarItem onClick={props.go} data-to="Prof" indicator={<Badge mode="" aria-label="Новый раздел"  />} text="Настройки">
        <Icon28Settings />
      </TabbarItem>
</Tabbar>





<Button stretched size="l" mode="secondary" onClick={props.go} data-to="persik">
			Создание ботов VK
				</Button>



      


	</Panel>
);




Prof.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};



export default Prof;



